.info {
  margin: 30px auto 0;
  max-width: 1340px;
  width: 100%;
}

.info__heading {
  font-family: "Piazzolla";
  font-style: normal;
  font-weight: 400;
  font-size: 35px;
  line-height: 50px;
  color: #f2edff;
  margin: 0;
}

.info__containers {
  margin: 50px 0 0;
}

.info-item {
  margin: 0 0 60px;
  width: 100%;
  display: flex;
  gap: 20px;
}

.info__container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 820px;
}

.info__main {
  width: 100%;
  display: flex;
  gap: 20px;
}

.info__title {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 25px;
  line-height: 34px;
  color: #f2edff;
  margin: 0;
}

.info__number {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 25px;
  line-height: 34px;
  color: #9747ff;
  margin: 0;
}

.info__description {
  margin: 0;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  color: #f2edff;
}

.info__items {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
}

.info__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.info__image {
  width: 116px;
  height: 116px;
  border-radius: 8px;
  background-color: #ffffff;
}

.info__name {
  margin: 0;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #f2edff;
}
