.form {
  margin: 50px auto 0;
  max-width: 1340px;
  width: 100%;
}

.form__heading {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1319px;
}

.form__title {
  margin: 0;
  max-width: 524px;
  font-family: "Piazzolla";
  font-style: normal;
  font-weight: 400;
  font-size: 35px;
  line-height: 50px;
  color: #f2edff;
}

.form__info {
  display: flex;
  align-items: center;
  gap: 20px;
}

.form__question {
  background: url(../images/logos/form__question.png) center no-repeat;
  background-size: 20px 20px;
  width: 20px;
  height: 20px;
  transition: all 0.3s ease;
}
.form__question:hover {
  opacity: 0.8;
}

.form__subtitle {
  margin: 0;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  color: #f2edff;
}

.form__form {
  margin: 40px 0 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  gap: 50px;
}

.form__inputs {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.form__input {
  box-sizing: border-box;
  border: none;
  text-align: left;
  padding: 10px 20px;
  gap: 10px;
  width: 252px;
  height: 54px;
  background: #f2edff;
  border-radius: 8px;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  color: #222027;
}

.form__submit {
  box-sizing: border-box;
  border: none;
  padding: 10px 20px;
  width: 252px;
  height: 54px;
  background: #bd8cfe;
  border-radius: 8px;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  text-align: center;
  color: #222027;
  margin: 0;
}
